import React from "react";
import { bible } from "../Utilities";

const letterStyle = {
    first: {
        fontFamily: "FrederickatheGreat-Regular", 
        fontSize: "xx-large"
    }
}

function Chapter(props) {
    const {bookName, bookEngShort, chapter, markedVerse} = props;

    const chapterVerses = bible.filter(item => (
            item.book === bookEngShort & Number(item.chapter) === Number(chapter)
        ))
    
    const FormattedVerse = (props) => {
        const {verse} = props;

        if (verse.verse_no === 1) {
            return (
                <>
                    &nbsp; &nbsp;
                    <sup>
                        {verse.verse_no}
                    </sup>
                    <span style={ letterStyle.first }>{verse.verse_text.charAt(0)}</span>{verse.verse_text.slice(1)}
                </>
            )
        } else {
            return (
                <>
                    &nbsp;
                    <sup>
                        {verse.verse_no}
                    </sup>
                    {verse.verse_text}
                </>
            )
        }
        
    }

    const verseHighlighted = {
        background: "yellow",
        opacity: 0.8
    };

    return(
        <div className="chapter">
            <h3>{chapter}. {bookName}</h3>
            <hr></hr>
            {
                chapterVerses.map(verse => (
                    verse._id === markedVerse ?
                    <span style={verseHighlighted} key={verse._id}>
                        <FormattedVerse verse={verse} /> 
                    </span> :
                    <span key={verse._id}>
                        <FormattedVerse verse={verse} /> 
                    </span>
                    )
                )
            }
        </div>
    );
}

export default Chapter;